export default {
	data() {
		return {
			list: []
			
		}
	},
	onLoad() {
		let list = [{}];
		for (let i = 0; i < 5; i++) {
			list[i] = {};
			list[i].name = String.fromCharCode(65 + i);
		}
		this.list = list;
	},
	methods: {
		
	}
}
